.booknow {
 display:flex;
 flex-direction:column;
 position: relative; 
 text-align:right;
 margin-left:auto;
 margin-right: 0;
  padding: 24px;
  color: white;
  width: 30%;
  z-index:100;
}

.book-button {
  font-size: 1.1rem;
  font-weight: 400;
  padding: 15px 32px;
  background-color:rgb(218, 236, 246);
  border: none;
  border-radius: 12px;
  margin: 4px;
  cursor: pointer;
}

button:hover {
  color: rgb(120, 63, 63);
}